import React from 'react'
import { Button, Toast } from 'antd-mobile';
import '../assets/css/product.css';
import axios from 'axios'

class Product extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            jsapiParameters: [],
            loading: false,
            pasword: password,
            orderid: '',
            flag: false,
            price: 0,
            signal_id: 0,
            tpl_name: '',
            diff_time: diff_time,
            hour: '00',
            min: '00',
            second: '00',
            price_list: [],
            img: require('../assets/img/logo.png'),
            img_payed: require('../assets/img/round_check.png')
        }
    }

    componentDidMount(){
        for(let i=0; i<price_list.length; i++){
            if(i == 0){
                price_list[i].selected = true;
            }
            else{
                price_list[i].selected = false;
            }
        }

        if(price_list.length > 0) {
            this.setState({
                price_list: price_list,
                price: price_list[0].price,
                signal_id: price_list[0].signal_id,
                tpl_name: price_list[0].name
            });
        }

        if(diff_time > 0){
            this.timer(diff_time);
        }
    }

    timer(seconds){
        var tt = setInterval(()=>{
            let day = Math.floor(seconds/(24 * 60 * 60));
            //let hour = Math.floor((seconds - (24 * 60 * 60) * day)/(60 * 60));
            let hour = Math.floor(seconds/(60 * 60));
            let minute = Math.floor((seconds - (24 * 60 * 60) * day - (60 * 60) * hour)/60);
            let second = Math.floor(seconds - (24 * 60 * 60) * day - (60 * 60) * hour - minute * 60);
            let day_html = day >= 10 ? day : '0' + day;
            let hour_html = hour >= 10 ? hour : '0' + hour;
            let minute_html = minute >= 10 ? minute : '0' + minute;
            let second_html = second >= 10 ? second : '0' + second;

            this.setState({
                hour: hour_html,
                min: minute_html,
                second: second_html
            });

            seconds--;
            if(seconds == 0){
                clearInterval(tt);
            }
        }, 1000);
    }

    jsApiCall(){
        let that = this;
        Toast.hide();
        WeixinJSBridge.invoke
        (
            'getBrandWCPayRequest',
            that.state.jsapiParameters,
            function(res)
            {
                // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
                if(res.err_msg == "get_brand_wcpay_request:ok")
                {
                    //success
                    Toast.loading('获取密码中…', 30);
                    that.password();
                }
                else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                    //支付取消
                    Toast.fail('支付取消', 2);
                }
                 else
                {
                    //支付失败
                    Toast.fail('支付失败', 2);
                }
            }
        );
    }

    callpay(){
        let that = this;
        if (typeof WeixinJSBridge == "undefined"){
            if( document.addEventListener ){
                document.addEventListener('WeixinJSBridgeReady', that.jsApiCall, false);
            }else if (document.attachEvent){
                document.attachEvent('WeixinJSBridgeReady', that.jsApiCall);
                document.attachEvent('onWeixinJSBridgeReady', that.jsApiCall);
            }
        }else{
            that.jsApiCall();
        }
    }

    submit(){
        if(this.state.price <= 0){
            Toast.info('请选择套餐');
            return false;
        }

        if(!this.state.loading) {
            Toast.loading('订单创建中…', 30);
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: '/wxpay/prepay',
                data: {
                    money: this.state.price,
                    signal_id: this.state.signal_id,
                    tpl_name: this.state.tpl_name,
                    uid: uid,
                    device_id: device_id
                },
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    this.setState({
                        jsapiParameters: JSON.parse(response.data.data.json),
                        orderid: response.data.data.orderid,
                        loading: false
                    });

                    this.callpay();
                }
                else{
                    Toast.hide();
                    Toast.fail('服务器出错', 2);
                    this.setState({
                        loading: false
                    });
                }
            }.bind(this)).catch(function (error) {
                Toast.hide();
                Toast.fail('未知错误', 2);
                this.setState({
                    loading: false
                });
            }.bind(this));
        }
    }

    password(){
        if(!this.state.loading) {
            let orderid = this.state.orderid;
            this.setState({
                loading: true
            });

            axios({
                method: 'post',
                url: '/wxpay/password',
                data: {
                    orderid: orderid
                },
            }).then(function (response) {
                if (response.status == 200 && response.data.code == 0) {
                    Toast.hide();
                    let json = JSON.parse(response.data.data.json);
                    this.setState({
                        pasword: json.pwd,
                        loading: false,
                        flag: true
                    });

                    this.timer(json.time);
                }
                else{
                    this.setState({
                        loading: false
                    });
                    if(!this.state.flag){
                        setTimeout(()=>{
                            this.password();
                        }, 300)
                    }
                }
            }.bind(this)).catch(function (error) {
                Toast.hide();
                Toast.fail('未知错误', 2);
                this.setState({
                    loading: false
                });
            }.bind(this));
        }
    }

    select(val, key){
        let list = this.state.price_list;
        for(let i=0; i<list.length; i++){
            list[i].selected = false;
            if(i == key){
                list[i].selected = true;
            }
        }

        this.setState({
            price: val.price,
            signal_id: val.signal_id,
            tpl_name: val.name,
            price_list: list
        });
    }

    render() {
        let passHtml;
        if(this.state.pasword){
            passHtml = (
                <div className="pasword">
                    <img className="payimg" src={this.state.img_payed}/>
                    <div className="success">支付成功</div>
                    <div className="memo">请在机器设备中输入以下消费码</div>
                    <div className="pwd">{this.state.pasword}</div>
                    <div className="time-cont">
                        <span className="title">有效时间还剩：</span>
                        <span className="point hour">{this.state.hour}</span>
                        <span className="maohao">:</span>
                        <span className="point minute">{this.state.min}</span>
                        <span className="maohao">:</span>
                        <span className="point second">{this.state.second}</span>
                    </div>
                </div>
            );
        }

        return (
            <div className="content">
                <div className="img">
                    <img src={this.state.img}/>
                    <div className="title">骏宝闪充</div>
                </div>
                <ul>
                    {this.state.price_list.map((item, index) => {
                        return (
                            <li className={item.selected ? 'price-wrapper active' : 'price-wrapper'} onClick={this.select.bind(this, item, index)}>
                                <div className="inner">
                                    <div className="price">{item.price}元/</div><div className="name">{item.signal_id==0 ? '半小时' : item.signal_id==1 ? '1小时' : item.signal_id==2 ? '2小时' : item.signal_id==3 ? '3小时' : item.signal_id==4 ? '4小时' : item.signal_id==5 ? '5小时' : item.signal_id==6 ? '8小时' : item.signal_id==7 ? '12小时' : item.signal_id==8 ? '16小时' : item.signal_id==9 ? '25小时' : ''}</div>
                                </div>
                            </li>
                        )
                    })}
                </ul>

                <div className="desc title"><span className="about">说明事项</span></div>
                <div className="desc">1、请确保设备在通电状态下进行操作</div>
                <div className="desc">2、请直接选择所需要的充电时长进行支付</div>
                <div className="desc">3、支付成功后，显示密码，计时开始；倒计时结束后，设备会断开充电</div>
                <div className="desc">4、输入正确密码后，绿色指示灯亮起，设备开放充电，请支付成功后尽快输入密码</div>
                <div className="desc">5、设备断电重启后，默认断开充电，需要再次扫码进行验证</div>
                <div className="desc">6、提供三种充电接口(苹果/安卓/Type-C)</div>
                <Button type="primary" className="pay" onClick={this.submit.bind(this)}>同意并支付</Button>
                { passHtml }
            </div>
        );
    }
}

export default Product




